import React, {Component} from 'react';
import logo from './brand.png';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';
import 'font-awesome/css/font-awesome.min.css';
import './App.css';
import { AnimatedSwitch } from 'react-router-transition';
import {Welcome, ThankYou} from './Welcome.js';
import {SecondStep, Rate} from './SecondStep.js';
import ThirdStep from './ThirdStep';
import FirstStep from './PersonalInfoForm.js';
import {
  FacebookShareButton,
  TwitterShareButton,
  WhatsappShareButton,
  FacebookIcon,
  TwitterIcon,
  WhatsappIcon
} from 'react-share';

import $ from 'jquery';
import {WiFi,EffectiveTransportation,Accissibility,Bicycle,StreetFurniture,ShoppingMall,ChashLess} from './LastPageParts';
import {BrowserRouter as Router, Route, Switch,Link} from 'react-router-dom';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state={
      user:{}
    }
  }
  update=(e,submit=0)=>{
    // this.setState((previous,e)=>{
    //   user:Object.assign(previous,e)
    // })
  var   tmp=Object.assign(this.state.user,e);
    this.setState({user:tmp});
    // this.setState({user:e});
    if(submit){
      $.ajax({
        method:"POST",
        url:'index.php/api/addSurvey',
        // url:'localhost/survey/index.php/api/addSurvey',
        data:this.state.user,
        datatype:'JSON',
        success:(res)=>{
          this.setState({user:{}});
          console.log('Submitted Successfully...');
        },
        fail:(res)=>{
          console.log('failure ...');
        }
      })
    }
    // console.log("Object Length: ",Object.keys(this.state.user).length);
  }
  routerChanged = ()=>{
  //   $('html,body').animate({'scrollTop':"0px"},1500);
  }
  
  render() {
    return (
      <div >
        <nav className="navbar navbar-light ">
          <Router onChange={this.routerChanged()}>
            <Link className="navbar-brand" to="/#/"><img src={logo}/></Link>
          </Router>
          <div className="d-flex">
            <a className="ml-2 mr-2 shareBtn" href="javascript:void(0)">
              Share : 
            </a>

          <WhatsappShareButton  className="ml-1 d-block d-md-none" title={"Participate in our Cyberjaya Satisfaction Survey 2018 to improve Cyberjaya now!  "} separator=" " size={32} url={"http://survey2018.cyberjayamalaysia.com.my"}>
              <i className="fa fa-whatsapp"></i>
          </WhatsappShareButton>
            

          <FacebookShareButton className="ml-3"  size={32} url={"http://survey2018.cyberjayamalaysia.com.my"}  >
              <i className="fa fa-facebook-f"></i>
          </FacebookShareButton>
          <TwitterShareButton  className="ml-3" title={"Participate in our Cyberjaya Satisfaction Survey 2018 to improve Cyberjaya now!  " } size={32} url={"http://survey2018.cyberjayamalaysia.com.my"} >
          
             <i className="fa fa-twitter"></i>
           
          </TwitterShareButton>
            
          </div>
        </nav>
        <Switch>

          {/* <AnimatedSwitch   atEnter={{ opacity: 0 }}
                          atLeave={{ opacity: 0 }}
                          atActive={{ opacity: 1 }}
                          className="switch-wrapper">
             */}[
            <Route  path="/steps/:step([1])" render={(props)=><FirstStep classes=""  {...props} parentFn={this.update} user={this.state.user}/>}/>
            <Route  path="/steps/:step([2])" render={(props)=><SecondStep  {...props} parentFn={this.update} user={this.state.user}/>}/>            
            {
             window.innerWidth<768 ?   //this is to separate last screen if on mobile 
             <Route  path="/steps/:step([3-9])"
               render={(props)=><ThirdStep {...props} parentFn={this.update} user={this.state.user} 
               item={
                 props.match.params.step=='3'?<WiFi user={this.state.user} {...props}  parentFn={this.update} />:
                 props.match.params.step=='4'?<EffectiveTransportation user={this.state.user} {...props}  parentFn={this.update}/>:
                 props.match.params.step=='5'?<Accissibility user={this.state.user} {...props}  parentFn={this.update}/>:
                 props.match.params.step=='6'?<Bicycle user={this.state.user} {...props}  parentFn={this.update}/>:
                 props.match.params.step=='7'?<StreetFurniture user={this.state.user} {...props}  parentFn={this.update}/>:
                 props.match.params.step=='8'?<ShoppingMall user={this.state.user} {...props}  parentFn={this.update}/>:
                 props.match.params.step=='9'?<ChashLess user={this.state.user} {...props}  parentFn={this.update}/>:''
                 
                }/>}  />
                : <Route path="/steps/:step([3])" render={(props)=><ThirdStep {...props} parentFn={this.update} user={this.state.user} 
                item={[//if not on moobile will add the 
                  <WiFi user={this.state.user}  parentFn={this.update} />,
                  <EffectiveTransportation user={this.state.user}  parentFn={this.update}/>,
                  <Accissibility user={this.state.user}  parentFn={this.update}/>,
                  <Bicycle user={this.state.user}  parentFn={this.update}/>,
                  <StreetFurniture user={this.state.user}  parentFn={this.update}/>,
                  <ShoppingMall user={this.state.user}  parentFn={this.update}/>,
                  <ChashLess user={this.state.user}  parentFn={this.update}/>
                ]
                }/>}/>  
             }
                <Route exact path="/steps/([4-9]|10)" component={ThankYou} />

            <Route exact path="/" component={Welcome}/>
          {/* </AnimatedSwitch> */}
             ]
        </Switch>
      </div>
    );
  }
}
export default App;



