import React, {Component} from 'react';
import {Welcome} from './Welcome';
import Progress from './Progress';
import {Link} from 'react-router-dom';
import $ from 'jquery';
class FirstStep extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      user: {
        gender:props.user.gender|| 'Male',
        age:props.user.age|| '18-25',
        nationality:props.user.nationality|| 'Malaysian',
        connectionType:props.user.connectionType|| 'studying',
        name:props.user.name|| '',
        email:props.user.email|| ''
      }
    };
  }
 
  _handleChange = (e) => {
    var tmp = this.state.user;
    tmp[e.target.name] = e.target.value;
    this.setState({user: tmp});
    // console.log(this.state.user);
  }
  _submit = (e)=>{
    e.preventDefault();
    
    window.location.hash='steps/2';
  }
  componentDidMount(){
    $('html,body').animate({'scrollTop':"0px"},1500);
  }  
  
  componentWillUnmount(e){
    // console.log('unmount personal');
    this.props.parentFn(this.state.user);
  }

  render() {
    return (
      
      [<Welcome classes="header d-none d-sm-block" />,<Progress classes="progress_0"/>,
      <form onSubmit={this._submit} className=" flex-column m-0 d-flex mx-auto col-sm-12 col-md-9 col-xl-5 col-12 mt-4 mb-4">

        <input
          type="text"
          onChange={this._handleChange}
          value={this.state.user.name}
          name="name"
          
          required
          placeholder="Full name (as per ic/passport)"/>
        <input
          type="email"
          name="email"
          onChange={this._handleChange}
          value={this.state.user.email}
          
          required
          placeholder="Email"/>
        <h5>Gender</h5>
        <div className="row ml-1">
          <label className="col-sm-3 col-6 ">
            <input
              onChange={this._handleChange}
              type="radio"
              name="gender"
              value="Male"
              checked={this.state.user.gender == 'Male'}/>
            Male
          <span className="thirdCheckMark"></span>
          </label>
          <label className="col-sm-3 col-6">
            <input
              onChange={this._handleChange}
              type="radio"
              name="gender"
              value="Female"
              checked={this.state.user.gender == 'Female'}/>
            Female
          <span className="thirdCheckMark"></span>
          </label>
        </div>
        <h5>Age</h5>
        <div className="row ml-1">
          <label className="col-sm-3 col-6 ">
            <input
              type="radio"
              onChange={this._handleChange}
              name="age"
              value="18-25"
              checked={this.state.user.age == '18-25'}/>
            18-25
          <span className="thirdCheckMark"></span>
          </label>
          <label className="col-sm-3 col-6">
            <input
              type="radio"
              name="age"
              onChange={this._handleChange}
              value="26-40"
              checked={this.state.user.age == '26-40'}/>
            26-40
          <span className="thirdCheckMark"></span>
          </label>
          <label className="col-sm-3 col-6 ">
            <input
              type="radio"
              name="age"
              onChange={this._handleChange}
              value="41-and-above"
              checked={this.state.user.age == '41-and-above'}/>
            41 and above
          <span className="thirdCheckMark"></span>
          </label>
        </div>
        <h5>Nationality</h5>
        <div className="row ml-1">
          <label className="col-sm-3 col-6 ">
            <input
              type="radio"
              name="nationality"
              value="Malaysian"
              onChange={this._handleChange}
              checked={this.state.user.nationality == 'Malaysian'}/>
            Malaysian
          <span className="thirdCheckMark"></span>
          </label>
          <label className="col-12 col-sm-9 ">
            <input
              type="radio"
              name="nationality"
              value="Non-Malaysian"
              onChange={this._handleChange}
              checked={this.state.user.nationality == 'Non-Malaysian'}/>
            Non-Malaysian (please state your nationality):
          <span className="thirdCheckMark"></span>
          </label>
            <input type="text" className="col-sm-9 col-12 offset-sm-3" name="nationality" placeholder="Your nationality"/>
        </div>

        <h5>Primary Connection to Cyberjaya</h5>
        <div className="row ml-1">
          <label className="col-sm-auto col-6">
            <input
              type="radio"
              name="connectionType"
              value="studying"
              onChange={this._handleChange}
              checked={this.state.user.connectionType == 'studying'}/>
            Studying
          <span className="thirdCheckMark"></span>
          </label>
          <label className="col-sm-auto col-6">
            <input
              type="radio"
              name="connectionType"
              value="working"
              onChange={this._handleChange}
              checked={this.state.user.connectionType == 'working'}/>
            Working
          <span className="thirdCheckMark"></span>
          </label>
          <label className="col-sm-auto col-12">
            <input
              type="radio"
              name="connectionType"
              value="residing"
              onChange={this._handleChange}
              checked={this.state.user.connectionType == 'residing'}/>
            Residing in Cyberjaya
          <span className="thirdCheckMark"></span>
          </label>
          <label className="col-sm-auto col-12">
            <input
              type="radio"
              name="connectionType"
              value="buisnessOwner"
              onChange={this._handleChange}
              checked={this.state.user.connectionType == 'buisnessOwner'}/>
            Business owner
          <span className="thirdCheckMark"></span>
          </label>
        </div>

            {/* <Link  style={{display:'block',height:"100%"}} to={'/steps/'+(parseInt(this.props.match.params.step)+1)}   className="btn firstMove mx-auto w-50 mt-4">Next</Link> */}
            <button  type="submit" className="btn firstMove mx-auto w-50 mt-4">
              Next
            </button>
      </form>]
    )
  }
}
export default FirstStep;


