import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import {Rate} from './SecondStep';
import {Welcome} from './Welcome';
import Progress from './Progress';
import $ from 'jquery';

class WiFi extends React.Component{
  componentDidMount(){
    $('html,body').scrollTop(0);
  }  
  constructor(props) {
    super(props);

    this.state = {
      user: {
        wifiCoverage:props.user.wifiCoverage|| 3,
        wifiSpeed:props.user.wifiSpeed|| 3
      
       }
    }
  }
  _handleChange = (e) => {
    var tmp = this.state.user;
    tmp[e.target.name] = e.target.value;
    this.setState({user: tmp});
    this.props.parentFn(this.state.user);
  }
    render (){
        return (
            [
            <div className="row m-0  col-sm-12 col-xl-7 col-lg-9  col col-12 mx-auto p-0">
            <h5 className="col-12 col-xl-7 col-lg-7">Cyberjaya Free WiFi Services</h5>
            <h6 className="col-12 col-xl-7 col-lg-7 col-sm-12">a. Coverage</h6>
            {[1, 2, 3, 4, 5]
                .map(function (element,index) {
                return (
                    <label className={'col value_'+element} key={index}>
                    <input
                        type="radio"
                        value={element}
                        name="wifiCoverage"
                        checked={this.state.user.wifiCoverage == element}
                        onChange={this._handleChange}/>
                    <span className="secondCheckMark"></span>
                    </label>
                )
                }, this)
                }
            <input type="text"  placeholder="Remarks:" className="col-12 col-sm-12 col-md-12 col-xl-7 col-lg-7 remark"/>
            </div>,
            <div className="row m-0  col-sm-12 col-xl-7 col-lg-9  col col-12 mx-auto p-0">
            <h6 className="col-12 col-xl-7 col-lg-7 col-sm-12">b. Speed</h6>
            {[1, 2, 3, 4, 5]
                .map(function (element,index) {
                return (
                    <label className={'col value_'+element} key={index}>
                    <input
                        type="radio"
                        value={element}
                        name="wifiSpeed"
                        checked={this.state.user.wifiSpeed == element}
                        onChange={this._handleChange}/>
                    <span className="secondCheckMark"></span>
                    </label>
                )
                }, this)}
            </div>
            ]
        )
    }
}


