class ChashLess extends Component{
  componentDidMount(){
    $('html,body').scrollTop(0);
  }  
  constructor(props) {
    super(props);

    this.state = {
      user: {
        cashBySmartphone:props.user.cashBySmartphone||"1",
        preferCashless:props.user.preferCashless||"1"
       }
    }
  }
  _handleChange = (e) => {
    var tmp = this.state.user;
    tmp[e.target.name] = e.target.value;
    this.setState({user: tmp});
    this.props.parentFn(this.state.user);

  }
    render=()=>{
        return ([
            <div className="row m-0  col-sm-12 col-xl-7 col-lg-9  col col-12 mx-auto p-0">
            <p className="col-12" id="answerQuestions">Please answer the following questions by ticking the boxes with either yes or no</p>
            <h5 className="col-12 col-xl-7 col-lg-7"> Cyberjaya Cashless Society Project</h5>
              <div className="col-7"></div>
              <h5 className="col-12 col-xl-8 col-lg-8">a. Do you think cashless payment using smartphone
                will make your life more convenient in Cyberjaya?</h5>
    
              <label className="col ml-4">
                <input
                  type="radio"
                  name="cashBySmartphone"
                  value="1"
                  checked={this.state.user.cashBySmartphone=="1"}
                  onChange={this._handleChange}/>Yes
              <span className="secondCheckMark"></span>
              </label>
              <label className="col">
                <input
                  type="radio"
                  name="cashBySmartphone"
                  value="0"
                  checked={this.state.user.cashBySmartphone=="0"}
                  onChange={this._handleChange}/>No
              <span className="secondCheckMark"></span>
              </label>
    
            </div>,
            <div className="row m-0  col-sm-12 col-xl-7 col-lg-9  col col-12 mx-auto p-0">
              <div className="col-7"></div>
              <h5 className="col-12 col-xl-8 col-lg-8">b. Are you willing to use cashless payment using smartphone instead of using cash in Cyberjaya?</h5>
    
              <label className="col ml-4">
                <input
                  type="radio"
                  name="preferCashless"
                  value="1"
                  checked={this.state.user.preferCashless=="1"}
                  onChange={this._handleChange}/>Yes
              <span className="secondCheckMark"></span>
              </label>
              <label className="col">
                <input
                  type="radio"
                  name="preferCashless"
                  value="0"
                  checked={this.state.user.preferCashless=="0"}
                  onChange={this._handleChange}/>No
              <span className="secondCheckMark"></span>
              </label>
    
            </div>
        ])
    }
}
export {WiFi,EffectiveTransportation,Accissibility,Bicycle,StreetFurniture,ShoppingMall,ChashLess}


