import React, {Component} from 'react';
import {Link,Route} from 'react-router-dom';
import {Rate} from './SecondStep';
import {Welcome} from './Welcome';
import Progress from './Progress';
import {WiFi,EffectiveTransportation,Accissibility,Bicycle,StreetFurniture,ShoppingMall,ChashLess} from './LastPageParts';

export default class ThirdStep extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      user: {
        // wifiCoverage:props.user.wifiCoverage|| 3,
        // wifiSpeed:props.user.wifiSpeed|| 3,
        // transportaionRoute:props.user.transportaionRoute|| 3,
        // transportationEffeciency:props.user.transportationEffeciency|| 3,
        // accessibilityToCyber:props.user.accessibilityToCyber|| 3,
        // parking:props.user.parking|| 3,
        // bicycleLanes:props.user.bicycleLanes|| 3,
        // bicycleParking:props.user.bicycleParking|| 3,
        // busShelter:props.user.busShelter|| 3,
        // streelLighting: props.user.streelLighting|| 3,
        // trafficSignages:props.user.trafficSignages|| 3,
        // pedestrianWalkway:props.user.pedestrianWalkway|| 3,
        // shoppingMall:props.user.shoppingMall|| 3,
        // safety:props.user.safety|| 3,
        // cashBySmartphone:props.user.cashBySmartphone||"true",
        // preferCashless:props.user.preferCashless||"true"
       }
    }
  }

  _handleChange = (e) => {
    var tmp = this.state.user;
    tmp[e.target.name] = e.target.value;
    this.setState({user: tmp});
  }
  _submit=(e)=>{
    if(window.innerWidth<768) 
    { 
        if(this.props.match.params.step==9){
          this.props.parentFn(this.state.user,'submit');
        }
        else{
          this.props.parentFn(this.state.user);
        }
    }
    else{
      this.props.parentFn(this.state.user,'submit');
    }
  }
  
  render(props) {
    return (
     [ <Welcome classes="header d-none d-sm-block" />,<Progress classes={"progress_"+ this.props.match.params.step+'0'}/>,<form className="mt-sm-3 mb-3">
        {
          this.props.match.params.step!=9 &&
        <Rate  classes="d-none d-sm-none d-md-flex" radioClass="secondCheckMark" {...props}/>
        }
        {this.props.item}
        <div className="row m-0 justify-content-center col-12 mt-5 mb-5">
          <Link to={'/steps/'+(parseInt(this.props.match.params.step)-1)} className="btn col-md-2 col-4 col-xl-2 col-sm-3 mr-1 back">Back</Link>
           <Link onClick={this._submit} to={'/steps/'+(parseInt(this.props.match.params.step)+1)}  className="btn col-md-2 col-7 col-xl-2 col-sm-3 lastMove" >Next</Link>
        </div>
      </form>]
    )
  }
}


